.socialLinks .icon {
  font-size: 1.5rem;
  color: #1d3a64;
}

.socialLinks a {
  height: 1.5rem;
  width: 1.5rem;
  background-color: white;
  padding: 0.5rem;
  border-radius: 4rem;
  color: #1d3a64;
  display: flex;
}

.socialLinks a:hover .icon {
  color: #152c4c;
}

.socialLinks {
  font-size: 1.8rem;
  width: 9rem;
  z-index: 100;
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 600px) {
  .socialLinks a {
    background-color: #1d3a64;
  }

  .socialLinks .icon {
    font-size: 1.5rem;
    color: white;
    stroke-width: 1.6;
  }
}
