.app {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  background: linear-gradient(360deg, #ffffff 25.7%, #eaf2f9 100%);
}

main {
  display: flex;
  justify-content: center;
}

.landing {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100vh;
  max-height: 70rem;
  justify-content: space-between;
}

.titleTextContainer {
  margin: auto;
  max-width: 25rem;
  padding-left: 2rem;
  padding-bottom: 10rem;
  align-self: center;
  justify-self: center;
  z-index: 100;
}

.title {
  font-size: 2.5rem;
}

.subTitle {
  font-size: 1.5rem;
}

.description {
  margin-top: 2rem;
  font-style: normal;
}

.buttonWrapper {
  display: inline-block;
  margin-top: 2rem;
}

.artworkContainer {
  height: 100%;
  width: 50%;
  position: relative;
}

.sketch {
  position: absolute;
  top: -6rem;
  right: 0rem;
  height: 100%;
  max-width: 50vw;
}

.projectsHeadline {
  margin: 3rem auto;
}

.projectsContainer {
  margin: 0 auto;
  align-items: center;
  min-height: 80vh;
  z-index: 100;
}

ul {
  padding: 0;
  margin: 0;
}

.projectContainer {
  display: inline-block;
  margin: 1rem;
}

.projectsBackgroundContainer {
  position: absolute;
  top: 100vh;
  z-index: 1;
  height: 100vh;
}

.projectsBackground {
  height: 150%;
}

.logo {
  height: 2rem;
  margin-right: 0.8rem;
}

.leftHeader {
  display: flex;
}

nav a:hover {
  color: #1e40c8;
}

header {
  display: flex;
  justify-content: space-between;
  margin: 1.5rem;
  margin-left: 5rem;
  z-index: 10;
}

nav {
  margin-left: 6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 15rem;
  max-width: 20rem;
  font-weight: 600;
}

nav a:hover {
  color: #1e40c8;
}

.logoText {
  font-size: 1.5rem;
  font-weight: 800;
  display: flex;
  align-items: center;
}

footer {
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
  text-align: center;
}

.socialMediaFooter {
  margin: 6rem 0;
  color: rgb(29, 29, 29);
  font-size: 1.8rem;
  width: 14rem;
  display: flex;
  justify-content: space-between;
}

.socialMediaFooter a {
  height: 4rem;
  width: 4rem;
  background-color: rgb(182, 62, 62);
  border-radius: 4rem;
  color: #1d3a64;
  display: flex;
  align-items: center;
  justify-content: center;
}

.socialMediaFooter a:nth-child(1) {
  background-color: rgb(182, 62, 62);
}

.socialMediaFooter a:hover:nth-child(1) {
  background-color: rgb(209, 71, 71);
}

.socialMediaFooter a:nth-child(2) {
  background-color: #0e76a8;
}

.socialMediaFooter a:hover:nth-child(2) {
  background-color: rgb(18, 148, 208);
}

.socialMediaFooter a:nth-child(3) {
  background-color: #24292e;
}

.socialMediaFooter a:hover:nth-child(3) {
  background-color: rgb(57, 65, 72);
}

.socialMediaFooter .icon {
  font-size: 2rem;
  color: rgb(255, 255, 255);
}

.socialMediaFooter svg {
  stroke-width: 1.4;
}

@media screen and (max-width: 80rem) {
  .projectsContainer {
    width: 35rem;
  }
}
@media screen and (min-width: 80rem) and (max-width: 120rem) {
  .projectsContainer {
    width: 64rem;
  }
}

@media screen and (min-width: 120rem) and (max-width: 150rem) {
  .projectsContainer {
    width: 96rem;
  }
}

@media screen and (min-width: 150rem) {
  .projectsContainer {
    width: 128rem;
  }
}

@media only screen and (max-width: 900px) {
  nav {
    display: none;
  }

  .projectsContainer {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .projectContainer {
    margin: 0 0 1rem 0;
  }

  .projectsBackgroundContainer {
    display: none;
  }

  .buttonWrapper {
    display: none;
  }

  .sketch {
    top: -2rem;
    right: 0;
    max-width: 30vw;
  }
}
